.menu {
    margin-top: 20px;
    height: 50px;
    width: 100%;
    border: 1px solid;
    display: flex;
    align-items: center;
    background-color: #999999;
    position: fixed;
    z-index: 50;
}

.burgerBtn {
    width: 20px;
    height: 20px;
    margin-left: 20px;
    position: relative;
    cursor: pointer;
}

.burgerBtn:before {
    content: '';
    position: absolute;
    top: 0;
    width: 30px;
    background-color: black;
    height: 2px;
}

.burgerBtn span {
    position: absolute;
    top: 9px;
    width: 30px;
    background-color: black;
    height: 2px;
}

.burgerBtn:after {
    content: '';
    position: absolute;
    bottom: 0;
    width: 30px;
    background-color: black;
    height: 2px;
}
